<template>

    <div class="txt-box">
        <p>{{ fullMessage }}</p>
        <!-- <button type="button" @click="increment" :class="{ active: isActive, 'text-notrue': hasError }">Button</button> -->
        <a href="#none" @click="increment" :class="classObject"><span>Button</span></a>
        <!-- <button type="button" @click="increment" :class="classObject"><span>Button</span></button> -->
    </div>

    <!-- 클래스바인딩 (배열) -->
    <!-- <button type="button" @click="increment" :class="[activeClass, errorClass]">Button</button> -->
    <!-- 클래스바인딩 (삼항표현식) -->
    <!-- <button type="button" @click="increment" :class="[isActive.value ? activeClass : '', errorClass]">Button</button> -->

    <!-- $attrs (Composition API ver) :: 부모 컴포넌트에서 전달된 모든 속성을 포함하는 객체 -->
    <!-- <p :class="$attrs.class">안녀어어엉</p>
    <span>반갑다</span> -->


</template>

<script setup>
import { reactive, computed, ref } from 'vue';

const setItem = reactive({ 
    cnt : 0,
    message : 'Hello, Vue'
});

const fullMessage = computed(() => `${setItem.message} ${setItem.cnt}`);

const increment = () => {
    setItem.cnt++;
}

// TYPE 01-1 (클래스바인딩)
// const isActive = ref(true);
// const hasError = ref(false);

// TYPE 01-2 (클래스바인딩) 
// const classObject = reactive({
//     btns: true,
//     'text-notrue': false
// })

// TYPE 01-3 (클래스바인딩)
const isActive = ref(true);
const error = ref(null);

const classObject = computed(() => ({
    btn: true,
    't2': true,
    active: isActive.value && !error.value,
    //'text-notrue': error.value && error.value.type === 'fatal'
}))

// TYPE 02-1 (클래스바인딩:배열)
// const activeClass = ref('active')
// const errorClass = ref('text-notrue')

// TYPE 02-2 (클래스바인딩:삼항표현식 배열)
// const isActive = ref(false);
// const activeClass = 'active';
// const errorClass = 'text-danger';


</script>
